<template>
  <div class="golf-spec-badge">
    <div class="golf-spec-badge__badge" v-for="spec in specs" :key="spec">
      <span>{{ spec }}</span>
    </div>
  </div>
</template>
<script>
import { defineComponent, computed } from 'vue'

export default defineComponent({
  name: 'DeviceGolfSpecBadge',
  props: {
    length: {
      type: String,
      default: null
    },
    loft: {
      type: String,
      default: null
    },
    gender: {
      type: String,
      default: null
    },
    shaft_model: {
      type: String,
      default: null
    },
    shaft_material: {
      type: String,
      default: null
    }
  },
  setup(props, { emit }) {
    const specs = computed(() =>
      [
        props.loft,
        props.length,
        props.gender,
        props.shaft_model,
        props.shaft_material
      ].filter((spec) => !!spec)
    )
    return {
      specs
    }
  }
})
</script>
<style scoped lang="scss">
.golf-spec-badge {
  @apply inline-block;
  &__badge {
    @apply px-3 py-1 bg-teal-lighten inline-block rounded-full text-xs mr-1 text-grey-03;
  }
}
</style>
